@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

body {
  background-color: #1e1e1e;
  color: #f5f5f5;
}

a {
  text-decoration: none;
  color: inherit;
}

.info-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.info-card {
  background-color: #2d2d2d;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease;

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4);
  }

  .info-title {
    font-size: 28px;
    margin-bottom: 20px;
    text-align: center;
    color: #f5f5f5;
  }

  .info-description {
    font-size: 16px;
    text-align: center;
    margin-bottom: 20px;
    color: #ccc;
  }

  .info-image-map-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;

    .info-gallery {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;

      .info-gallery-image {
        width: 100%;
        max-width: 300px;
        height: auto;
        border-radius: 10px;
        cursor: pointer;
        transition:
          transform 0.3s ease,
          opacity 0.3s ease;

        &:hover {
          transform: scale(1.05);
          opacity: 0.9;
        }
      }
    }

    .info-map {
      width: 100%;
      max-width: 450px;
      height: 300px;
      border-radius: 10px;
      border: none;
    }
  }

  .info-back-button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    background-color: #848484;
    color: #fff;
    text-align: center;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #5a5a5a;
    }
  }
}

.info-fullscreen-gallery {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 1000;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;

  &.active {
    display: flex;
    opacity: 1;
  }

  .info-fullscreen-image {
    max-width: 90%;
    max-height: 90%;
    object-fit: contain;
    transition: transform 0.5s ease-in-out;
  }

  .info-gallery-button {
    position: absolute;
    background: none;
    border: none;
    color: white;
    font-size: 2rem;
    cursor: pointer;
    transition: color 0.3s ease;

    &:hover {
      color: #ccc;
    }
  }

  .info-close-gallery {
    top: 10px;
    right: 20px;
  }

  .info-prev-image {
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
  }

  .info-next-image {
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.info-reviews-container {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .info-review {
    background-color: #3a3a3a;
    border-radius: 10px;
    padding: 15px;
    margin-bottom: 10px;
    width: 100%;
    max-width: 600px;
    transition:
      transform 0.3s ease,
      box-shadow 0.3s ease;

    &:hover {
      transform: translateY(-5px);
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    }

    .info-review-name {
      font-weight: bold;
      margin-bottom: 10px;
      color: #f5f5f5;
    }

    .info-review-text {
      color: #ccc;
      margin-bottom: 10px;
    }

    .info-review-actions {
      display: flex;
      gap: 10px;

      .edit-review-button,
      .info-delete-button,
      .info-save-button {
        padding: 5px 10px;
        border: none;
        border-radius: 3px;
        cursor: pointer;
        font-size: 14px;
        transition: background-color 0.3s ease;

        &:hover {
          opacity: 0.8;
        }
      }

      .info-edit-button {
        background-color: #848484;
        color: white;
      }

      .info-delete-button {
        background-color: #5a5a5a; 
        color: white;
      }

      .info-save-button {
        background-color: #6c757d; 
        color: white;
      }
    }

    .info-edit-review-textarea {
      width: 100%;
      height: 80px;
      padding: 10px;
      margin-bottom: 10px;
      border: 1px solid #ccc;
      border-radius: 5px;
      background-color: #2d2d2d;
      color: #f5f5f5;
      resize: vertical;

      &:focus {
        outline: none;
        border-color: #848484;
      }
    }
  }
}

.info-review-form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .info-form-input,
  .info-form-textarea {
    width: 100%;
    max-width: 600px;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #3a3a3a;
    color: #f5f5f5;
    transition: border-color 0.3s ease;

    &:focus {
      outline: none;
      border-color: #848484;
    }
  }

  .info-form-textarea {
    resize: none;
    height: 100px;
  }

  .info-form-button {
    padding: 10px 20px;
    background-color: #848484;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #5a5a5a;
    }
  }
}
.info-review-actions {
  display: flex;
  gap: 10px;

  .info-edit-button,
  .info-delete-button,
  .info-save-button {
    padding: 5px 10px;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease;

    &:hover {
      opacity: 0.8;
    }
  }

  .info-edit-button {
    background-color: #848484;
