  }
}

.info-edit-review-textarea {
  width: 100%;
  height: 80px;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #2d2d2d;
  color: #f5f5f5;
  resize: vertical;

  &:focus {
