.cart {
  margin-bottom: 40px;
}
.cart__container {
  width: 100%;
  height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cart__container-cart {
  background-color: rgba(0, 0, 0, 0.274);
  border-radius: 7px;
  width: 750px;
  padding: 5px 5px 5px 5px;
}
.cart__container-cart2 {
  background-color: rgba(0, 0, 0, 0.274);
  border-radius: 7px;
  width: 750px;
  padding: 5px 5px 5px 5px;
  display: none;
}
.cart__container-title {
  font-size: 50px;
  font-weight: 400;
  padding-bottom: 50px;
}

@media (max-width: 425px) {
  .cart {
    margin-bottom: 20px;
  }
  .cart__container {
    height: auto;
  }
  .cart__container-cart2 {
    display: block;
    width: 90%;
    padding: 5px;
    border-radius: 5px;
  }
  .cart__container-cart {
    display: none;
  }
  .cart__container-title {
    font-size: 30px;
    padding-bottom: 20px;
  }
  .car {
    display: none;
  }
}